<template>
  <div class="board" :style="boardStyle">
    <div v-if="linkDirection" class="link" :class=" 'link-' + linkDirection "></div>
    <!-- <img class="board-img" :src="guideImage" height="170" width="120"> -->
    <!-- <img src="@/assets/img/guide/guide1.png" height="170" width="120"> -->
    <div class="board-container flex-column"
      :class="guideImageWidth === '170'? 'container-padding-left': ''">
      <p>
        <span class="guide-title">{{title}}</span>
        <span v-if="currentStep" class="guide-step">({{currentStep}}/{{totalStep}})</span>
      </p>
      <p v-if='content' class="guide-content flex-vertical-center" v-html="content">
      </p>
      <slot v-else class="guide-content flex-vertical-center" name='content'></slot>
      <div class="btn-group">
        <slot></slot>
        <el-button
          v-if="showQuitButton"
          type="text"
          style="color: #7f7f7f;margin-right: 10px;padding-left: 5px;padding-right: 5px;"
          class="btn-qiut"
          @click.native="quitTutorial">
          {{quitBtnText}}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';

export default {
  inject: ['vm'],
  props: {
    // 标题
    title: String,
    // 内容
    content: String,
    // 图片
    guideImage: String,
    // 当前步骤
    currentStep: String,
    // 总步骤
    totalStep: String,
    width: {
      type: String,
      default: '450',
    },
    height: {
      type: String,
      default: '200',
    },
    top: String,
    left: String,
    // 连接方向
    linkDirection: String,
    // 是否显示退出按钮
    showQuitButton: {
      type: Boolean,
      default: true,
    },
    quitBtnText: {
      type: String,
      default: i18n.t('guide.quit_tutorial'),
    },
    guideImageWidth: {
      type: String,
      default: '140',
    },
  },
  computed: {
    boardStyle() {
      // guideboard长宽不变，计算缩放后的偏移量
      let left = `${this.left * this.size + (this.size * this.width - this.width) / 2}px`;
      let top = `${this.top * this.size + (this.size * this.height - this.height) / 2}px`;
      switch (this.linkDirection) {
        case 'left': left = `${this.left * this.size}px`;
          break;
        case 'right': left = `${this.left * this.size + (this.size * this.width - this.width)}px`;
          break;
        case 'top': top = `${this.top * this.size}px`;
          break;
        case 'bottom': top = `${this.top * this.size + (this.size * this.height - this.height)}px`;
          break;
        default: break;
      }
      return {
        left,
        top,
        background: `url(${this.guideImage}) no-repeat`,
        backgroundSize: `${this.guideImageWidth}px ${this.height}px`,
        backgroundColor: '#fff',
        width: `${this.width}px`,
        height: `${this.height}px`,
      };
    },
    size() {
      return window.screen.availWidth < 1920 ? 1 : window.screen.availWidth / 1920;
    },
  },
  methods: {
    nextStep() {
      this.vm.$emit('nextStep');
    },
    quitTutorial() {
      this.vm.$emit('quitTutorial');
    },
    skip() {
      this.$emit('skip');
    },
  },
};
</script>

