<template>
  <div class="heightlight-area" :class="{'transparent': opacity, }" :style="style">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    width: String,
    height: String,
    top: String,
    left: String,
    // 背景是否透明
    opacity: {
      type: Boolean,
      default: true,
    },

    // highlight: {
    //   type: Boolean,
    //   default: true,
    // },
    // backgroundColor:{
    //   type: String,
    //   default: '#fff',
    // }
  },
  computed: {
    style() {
      return {
        width: `${this.width * this.size}px`,
        height: `${this.height * this.size}px`,
        top: `${this.top * this.size}px`,
        left: `${this.left * this.size}px`,
        // backgroundColor: this.opacity ? 'rgba(255, 255, 255, 0);' : '#fff',
        // boxShadow: this.highlight ? 'rgba(0, 0, 0, 0.5) 0px 0px 0px 9999px;' : null,
      };
    },
    size() {
      // return window.screen.availWidth / 1920;
      return window.screen.availWidth < 1920 ? 1 : window.screen.availWidth / 1920;
    },
  },
};
</script>

<style lang="scss" scoped>
.heightlight-area {
  transition: 0.5s all;
  position: absolute;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 0px 9999px;
}

.transparent {
  background-color: rgba(255, 255, 255, 0);
  transition: ease 0.5s;
}

.box-shadow-none {
  box-shadow: none;
}
</style>
