import GuideBoard from './components/GuideBoard';
import HighlightArea from './components/HighlightArea';

const loadImgMixin = {
  components: {
    GuideBoard,
    HighlightArea,
  },
  data() {
    return {
      endLoadFlag: false,
    };
  },
  methods: {
    endLoad() {
      this.endLoadFlag = true;
    },
  },
  computed: {
    screamSize() {
      return window.screen.availWidth < 1920 ? 1 : window.screen.availWidth / 1920;
    },
  },
};

export default loadImgMixin;
