<template>
  <div class="guide"
    :style="{
      height: `${1080*screamSize}px`,
      backgroundImage: `url(${ isCloth ? require('@/assets/img/guide/model2.png')
        : require('@/assets/img/guide/model.png') })`,
      backgroundSize: `${1920*screamSize}px ${1080*screamSize}px`,
      fontSize: `${100*screamSize}%` }">
    <highlight-area
      left="1480"
      top="540"
      width="96"
      height="120">
      <div id="highlight"
        style="height: 100%; width: 100%;cursor: pointer;">
        <!-- eslint-disable max-len -->
        <img ref="dragImg"
          :src="require('@/assets/img/guide/materail.jpg')"
          style="border-radius:6px;width:6.85em;height:6.85em"
          draggable>
      </div>
    </highlight-area>
    <!-- eslint-disable max-len -->
    <guide-board
      :guide-image="isCloth ? require('@/assets/img/guide/drag-materail2.png') : require('@/assets/img/guide/drag-materail.png')"
      :title="$t('guide.materialize_design')"
      :content="$t('guide.materialize_design_content')"
      current-step="3"
      total-step="4"
      top="700"
      left="1250"
      link-direction="left">
    </guide-board>
    <canvas class="previewer"
      :style="{ top: `${160 * screamSize}px`,
        left: `${100 * screamSize}px`,
      }"
      width="1100"
      height="700"
      ref="renderer"
      @drop="handleDropOnShoe"
      @dragover.prevent
      v-on="canvasEvent">
      <!-- <div class="loading" v-if="loadingStatus">
        <div class="content">
          <div class="progress">
            <span :style="`width: ${loadingStatus.percentage}%`" />
          </div>
          <div>
            <span class="percentage">{{loadingStatus.percentage}}%</span>
            Processing 3D model & material images
          </div>
        </div>
      </div> -->
    </canvas>
  </div>
</template>

<script>
import { loadD4 } from '@/utils/load-d4';
import { thumb } from '@/utils/oss/utils';

import mixinRenderer from '@/views/model/mixin-renderer';
import mixin from '../loadImgMixin';

const material = {
  alias: '',
  backingMaterial: '',
  collection: '',
  color: '',
  component: '',
  curtailImgGlass: ' ',
  customerNumber: 'q23534343412ew',
  density: null,
  densityUnit: '',
  finish: '',
  flat: thumb(require('@/assets/img/guide/YC190521-FLAT.png'), {
    width: 512,
    height: 512,
  }),
  flatGlass: ' ',
  id: '1203925894993940482',
  isPublic: 1,
  mOQ: null,
  mOQUnit: 'm',
  materialID: '1203925894993940482',
  materialNumber: '6565',
  materialThumbnail: 'https://sdtc-test.oss-cn-shenzhen.aliyuncs.com/furImg/1185080766011412482-1b19be606a3deeb0d8d450a5687f7f6d.png',
  mostSuitableFor: '[]',
  name: 'GuideMetal(1)',
  otherProperties: '',
  patterns: '',
  placeOfOrigin: '',
  productionTurnaround: null,
  productionTurnaroundUnit: 'days',
  renderArguments: {
    materialType: 'standardSG',
    diffuseMap: thumb(require('@/assets/img/guide/YC190521_COLOR.png'), {
      width: 512,
      height: 512,
    }),
    normalMap: thumb(require('@/assets/img/guide/YC190521_NORMAL.png'), {
      width: 512,
      height: 512,
    }),
    specularMap: thumb(require('@/assets/img/guide/YC190521_SPECULAR.png'), {
      width: 512,
      height: 512,
    }),
    heightMap: thumb(require('@/assets/img/guide/YC190521-HEIGHT.png'), {
      width: 512,
      height: 512,
    }),
    unitX: 41.5,
    unitY: 29.5,
  },
  samplePrices: null,
  samplePricesUnit: 'USD',
  season: '',
  sensoryBreathable: '',
  sensorySoftness: '',
  sensoryStretch: '',
  sensoryThickness: '',
  supplierAddress: '',
  supplierCode: '',
  supplierContact: '',
  supplierEmail: '',
  supplierId: null,
  supplierName: '',
  tags: [],
  thickness: null,
  thicknessUnit: 'mm',
  thumbnail: 'https://sdtc-test.oss-cn-shenzhen.aliyuncs.com/furImg/1185080766011412482-1b19be606a3deeb0d8d450a5687f7f6d.png',
  type: 6,
  updateTime: 1575873275939,
  uploadTime: 1575873275904,
  weight: null,
  weightUnit: 'g/m^2',
  width: null,
  widthUnit: 'mm',
};

const model = [{
  shoeId: 1,
  componentID: 1,
  componentName: 'model',
  url: 'https://sdtc-frontend.oss-cn-shenzhen.aliyuncs.com/rendering/models/mesh.ctm',
  uvTrans: '',
  mapSec: '',
  edgeArr: '',
  material: {
    materialID: 1,
    name: 'default',
    renderArguments: { furID: 0, furName: 'default', materialType: 'standardSG' },
  },
}];

export default {
  mixins: [mixin, mixinRenderer],
  props: ['isCloth'],
  data() {
    return {
      loading: false,
      loadingStatus: {
        percentage: 0,
      },
    };
  },
  mounted() {
    this.initPerview();
  },
  methods: {
    nextStep() {
      this.$emit('nextStep');
    },
    // init
    async initPerview() {
      await this.$nextTick();
      // eslint-disable-next-line
      const D4 = await loadD4();
      this.d4 = this.instanceD4(D4, this.$refs.renderer);
      this.canvasEvent = this._initCanvasEvent();
      if (this.isCloth) {
        await this.d4.initMaterialPreviewComponent();
      } else {
        await this.d4.loadModel(model);
      }

      const canvasDom = this.$refs.renderer;
      const params = {
        canvas: canvasDom,
        width: 1100,
        height: 700,
        resolution: window.devicePixelRatio,
      };
      canvasDom.width = params.width;
      canvasDom.height = params.height;
      this.d4.resizeRenderer(params.width, params.height, params.resolution);
    },
    // 处理拖拽事件
    async handleDropOnShoe(event) {
      const canvasDom = this.$refs.renderer;
      const param = {
        left: canvasDom.getBoundingClientRect().left,
        top: canvasDom.getBoundingClientRect().top,
        clickX: event.clientX,
        clickY: event.clientY,
        eventType: event.type,
      };
      const renderSuccess = await this.d4.getTargetFromScreen(param);
      if (!renderSuccess) return;
      this.d4.beforeChangeMaterial(material);
      setTimeout(() => { this.nextStep(); }, 5000);
    },
  },
};
</script>

<style lang="scss" scoped>
.guide {
  background: url("~@/assets/img/guide/model.png") no-repeat;
  // img {
  //   width: 100%;
  // }
}

.previewer {
  position: absolute;
  background-color: #fff;
}
